h1, p {
  font-family: Lato;
}
body { padding-top: 70px; }


.borderStyle{
  box-shadow: 0 4px 8px 4px rgba(0,0,0,0.2);
  height: 350px;
  width:100%;
  text-align: center;
  position:relative;
  padding-top:10px;
  border-radius: 5px;
}

.imgstyle{
height: 50%;
}
.foot{
  position:absolute;
  bottom:0;
  width:100%
}


.marginStyle{
  margin-bottom: 25px;
}

.cmpFoot{
  position:fixed;
  bottom:10px;
  right:30px;
}
.sticky{
  position:-webkit-sticky;
  position:sticky;
  bottom:0;
  right:0;
}

.btnStyle{
  margin-right: 10px;
}
.imgresponsive{
  height:240px;
  display: none;
}
